<template>
  <div class="alarm-content">
    <my-content-title :title='title'></my-content-title>
    <div id='myContentCenter'>
      <div id="myTable" class="myTable">
        <button class="my-btn btn-active btn3" @click="save">保存设置</button>
        <div id="tableContent">
          <router-view ref='child' @child-msg='updateIsShow' @child-load="finishLoad"></router-view>
          <div class="noData" v-if="isShow">缺少该报警类型的测点!</div>
          <my-loading :isLoad='loading' @changeLoad="changeLoading($event)"></my-loading>
        </div>

        <div class="controls-btn">
          <div class="btn-content">
            <router-link v-for="(btn,index) in btnData" :key="index" :to="{path:btn.name}"  @click.native="getTable(btn.name,btn.isDgm)" class="con-dgm-btn" v-show="btn.isShow">{{btn.textBtn}}</router-link>
          </div>
        </div>

      </div>
    </div>

  </div>
</template>

<script>
  import MyLoading from 'components/common/MyLoading';
  import MyContentTitle from 'components/common/MyContentTitle';
  import getApi from 'network/getApi'
  export default {
    name: "Alarm",
    data(){
      return{
        title:'报警设置',
        ins:0,//切换按钮active
        name:'ConAlarm',
        isAdd:false,
        isShow:false,
        btnData: [//按钮初始化
          {type:1,textBtn:"工况报警",name:'ConAlarm',isShow: true},
          {type:4,textBtn:"DGU2000V1工况报警",name:'V1ConAlarm',isShow: true},
          {type:5,textBtn:"TMS工况报警",name:'TmsConAlarm',isShow: true},
          {type:2,textBtn:"常规报警",name:'RegAlarm',isShow: true},
          {type:3,textBtn:"MHD报警",name:'MhdAlarm',isShow: true},
        ],
        loading:true,//加载中动画
      }
    },
    watch:{
      '$store.state.customInfo':{
          handler(value, oldValue) {
              if (value) {
                  if(value.chooseAlarmBtn){
                      this.btnData = value.chooseAlarmBtn;
                  }
              }
          },
          deep: true,
          immediate: true,
      }
    },
    mounted(){
      // 在点击标题的时候放大并重新渲染表格
      this.$bus.$on('reloadtable',()=>{
        if(this.$refs.child){
          this.$refs.child.hot.render()
        }
      })
      this.name = this.$route.name //刷新的时候让button 的active 正确

    },
    methods:{
      getTable(name){ //点击下标签的时候改变active，改变路由，刷新之后路由不变，active不变，点击左边测点设置 active会有问题
        /*this.$router.push({name:name})
        this.name = name*/

      },
      //改变loading状态，将值传给子组件
      changeLoading(msg){
        this.loading = msg;
      },
      save(){
        /* 保存按钮 */
        let changeData = this.$refs.child.changeList.changeData
        //修改测点信息
        if(!$.isEmptyObject(changeData)){
          getApi.updateAlarmInfo(changeData)
                  .then(res=>{
                    if(res.msg==0){
                      swal({
                        title: "提示",
                        text: '保存成功',
                        button: "确认",
                      }).then((value) => {
                                this.$router.go(0)
                              });

                    }
                    else if(res.msg==1){
                      console.log('参数为空！');
                      swal({
                        title: "提示",
                        text: '保存失败',
                        button: "确认",
                      });
                    }
                    else if(res.msg==2){
                      console.log('后台运行错误！');
                      swal({
                        title: "提示",
                        text: '保存失败',
                        button: "确认",
                      });
                    }
                    else if(res.msg==3){
                      console.log('获取json数据时为空！');
                      swal({
                        title: "提示",
                        text: '保存失败',
                        button: "确认",
                      });
                    }
                  })
                  .catch(err=>{
                    console.log('请求错误',err)
                  })
        }
        else{
          swal({
            title: "提示",
            text: '保存成功',
            button: "确认",
          })
        }
      },
      updateIsShow(value){
        this.isShow = value
      },
      finishLoad(value){
        this.loading = value;
      }
    },
    components:{
      MyContentTitle,
      MyLoading
    },

  }

</script>

<style lang="scss">
  @import "~assets/css/components/alarm.scss";
  .alarm-content{
    position: relative;
  }
</style>
