<template>
  <div class="my-content-header" @dblclick="dblclick">
    <span class="page-title">{{title}}</span>
  </div>
</template>

<script>
export default {
  name: "MyContentTitle",
  props: ["title"],
  data() {
    return {
      flag: true
    };
  },
  methods: {
    dblclick() {
      //放大
      let homeHeader = document.getElementById("homeHeader");
      let aside = document.getElementById("aside");
      let content = document.getElementById("content");
      let wHeight = document.documentElement.clientHeight;
      if (this.flag) {
        homeHeader.style.display = "none";
        aside.style.display = "none";
        if(wHeight < 800){
          content.style.height = "calc(100vh - 20px)"; //中间内容加大
        }
        else{
          content.style.height = "calc(100vh - 40px)"; //中间内容加大
        }
        this.$bus.$emit("reloadtable"); //主动渲染table
        this.$bus.$emit("reloadoverview"); //主动渲染总貌图
        this.flag = !this.flag;
      } else {
        //缩小
        homeHeader.style.display = "block";
        aside.style.display = "block";
        content.style.width = "100px"; //改变宽让宽自动渲染
        if(wHeight < 800){
          content.style.height = "calc(100vh - 90px)"; //中间内容加大
        }
        else{
          content.style.height = "calc(100vh - 120px)"; //中间内容加大
        }
        this.$bus.$emit("reloadtable"); //主动渲染table
        this.$bus.$emit("reloadoverview"); //主动渲染总貌图
        this.flag = !this.flag;
      }
    }
  }
};
</script>

<style scoped>
.my-content-header {
  /* width: 100%-15px;
   */
  box-sizing: border-box;
  height: 50px;
  line-height: 50px;
  background: #fff;
  font-size: 20px;
  color: #000;
  padding-left: 15px;
  cursor: pointer;
  margin-bottom: 5px;
}
</style>
