              </div>
            </div>
          </div>
  </div>
</template>

<script>
export default {
  name: "MyLoading",
  props:['isLoad'],
  data() {
    return {
    };
  },
  methods: {
    cancelLoad(){
        this.$emit('changeLoad',false);
    }
  }
};
