<style scoped>
/*加载中动画*/
.loadingBlock {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
}

#loading-center {
  width: 100%;
  height: 100%;
  position: relative;
}
#loading-center-absolute {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 50px;
  -webkit-animation: opacity 0.2s; 
}
.object {
  width: 8px;
  height: 50px;
  margin-right: 7px;
  background-color: #343c45;
  -webkit-animation: animate 1s infinite;
  animation: animate 1s infinite;
  float: left;
}
.loadfont {
